import React, { useEffect } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Header from './Header';
import MainFeaturedPost from './MainFeaturedPost';
import FeaturedPost from './FeaturedPost';
import {getArticles, setDarkMode} from '../../redux/reducers/articlesReducer'
import Footer from './Footer';
import { connect, useDispatch, useSelector } from 'react-redux';
import { FormControlLabel, Switch } from '@material-ui/core';
import { STAGEMAP } from '../../Helps/StageMap';

const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
  },
  switchBase: {
    padding: 1,
    '&$checked': {
      transform: 'translateX(16px)',
      color: theme.palette.common.white,
      '& + $track': {
        backgroundColor: '#52d869',
        opacity: 1,
        border: 'none',
      },
    },
    '&$focusVisible $thumb': {
      color: '#52d869',
      border: '6px solid #fff',
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border']),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});


const useStyles = makeStyles((theme) => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
  mode: {
    width:'100%',
    display:'flex',
    justifyContent:'center'
  }
}));

function Home({isDark, setDarkMode}) {
  const classes = useStyles();
  const dispatch = useDispatch()

  const selectPosts = useSelector(state => state.articles.articles)

  const featuredPosts = selectPosts.filter(el => el.stage===STAGEMAP.approved)

  const darkModeToLocal = () => {
    setDarkMode(true)
    if(!isDark) localStorage.setItem('dark', true)
    else localStorage.removeItem('dark')
  }

  useEffect(() => {
    dispatch(getArticles())
  },[dispatch])

  return (
    <React.Fragment>
      <FormControlLabel
        className={classes.mode}
        control={<IOSSwitch checked={isDark} onChange={darkModeToLocal} name="checkedB" />}
        label="Change color mode"
      />
      <CssBaseline />
      <Container maxWidth="lg">
        <Header title="LOGOS POLYTECHNIKOS" isAbout/>
        <main>
          <MainFeaturedPost/>
          <Grid container spacing={4}>
            {featuredPosts.map((post) => (
              <FeaturedPost key={post.id} post={post} />
            ))}
          </Grid>
          <Grid container spacing={5} className={classes.mainGrid}>
          </Grid>
        </main>
      </Container>
      <Footer title="LOGOS POLYTECHNIKOS" description="Created by DOIT" />
    </React.Fragment>
  );
}

const mapState = (state) => {
  return {
    isDark: state.articles.isDark,
  }
}

export default connect(mapState, {setDarkMode})(Home)